import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/content',
      name: 'content',
      component: resolve => require(['../components/common/Content.vue'], resolve)
    },
    {
      path: '/home',
      name: 'home',
      component: resolve => require(['../components/common/Home.vue'], resolve),
      children: [
        {
          path: '/content',
          name: 'content',
          component: resolve => require(['../components/common/Content.vue'], resolve)
        },
        {
          path: '/article',
          name: 'article',
          component: resolve => require(['../components/common/Article.vue'], resolve)
        }
      ]
    }
  ]
})



